import React, { Component } from 'react';

import './clock.css';

export default class Clock extends Component {

  state = {
    time: new Date()
  }

  componentDidMount() {
    this.timerID = setInterval(
      () => {this.tick()},
      1000
    )
  }

  tick() {
    this.setState({ time: new Date() });
  }

  render() {

    return (
      <div className="clock">
        Local Time {this.state.time.toLocaleTimeString()}
      </div>
    )
  }
}